<template>
	<div
		id="mapContainer"
		v-if="this.$store.state.showMenu"
		v-bind:style="{ width: 75 + '%', left: 25 + '%' }"
	></div>
	<div id="mapContainer" v-else></div>
</template>

<script>
var api = require("@/service");
import L from "leaflet";
import "leaflet-draw";
import "leaflet/dist/leaflet.css";
import "leaflet-draw/dist/leaflet.draw.css";
var map;

// fix default icon problem
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
	iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
	iconUrl: require("leaflet/dist/images/marker-icon.png"),
	shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});

export default {
	data() {
		return {
			osm_layer: L.tileLayer(
				"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
			),
			layercontrol: L.control.layers(),
			fullScreenMap: false,
			polygonMarkerOptions: {
				radius: 2,
				fillColor: "green",
				color: "green",
				weight: 2,
				opacity: 0.5,
				fillOpacity: 0.2,
			},
			pointMarkerOptions: {
				radius: 3,
				fillColor: "red",
				color: "red",
				weight: 3,
				opacity: 1,
				fillOpacity: 0.2,
			},
			drawControl: null,
		};
	},
	methods: {
		setupLeafletMap() {
			// init map
			map = L.map("mapContainer", {
				center: [51.11, 4.46],
				crs: L.CRS.EPSG3857,
				zoom: 11,
				zoomControl: true,
				preferCanvas: false,
			});
			// add osm tile layer
			this.osm_layer.addTo(map);
			// add scale
			L.control.scale().addTo(map);
			// add layer control
			this.layercontrol.addTo(map);
			this.layercontrol.addBaseLayer(this.osm_layer, "OSM layer");
		},
		addToolTip(feature, layer) {
			if (feature.properties && feature.properties.name) {
				layer.bindTooltip(feature.properties.name);
			}
		},
		displayFeatures(fc) {
			let fg;
			if (this.$store.state.edittingPlekollection) {
				fg = this.drawnItems;
			} else {
				fg = L.featureGroup().addTo(map);
			}

			fc.features.forEach((f) => {
				switch (f.geometry.type) {
					case "Point":
						if (this.$store.state.edittingPlekollection) {
							L.geoJSON(f, {
							    onEachFeature: this.addToolTip,
						    }).addTo(fg);
						} else {
							L.geoJSON(f, {
								pointToLayer: (feature, latlng) => {
									return L.circleMarker(
										latlng,
										this.pointMarkerOptions
									);
								},
								onEachFeature: this.addToolTip,
							}).addTo(fg);
						}
						break;
					case "Polygon":
					case "MultiPolygon":
						L.geoJSON(f, {
							style: this.polygonMarkerOptions,
							onEachFeature: this.addToolTip,
						}).addTo(fg);
						break;
					default:
						L.geoJSON(f, {
							onEachFeature: this.addToolTip,
						}).addTo(fg);
				}
			});
			if (!this.$store.state.edittingPlekollection) {
				this.layercontrol.addOverlay(fg, fc.props.name);
			}
		},
		displayFeatureCollections() {
            map.eachLayer((layer) =>{
                if(!layer._url){
                    map.removeLayer(layer)
                    this.layercontrol.removeLayer(layer)
                }
            });
			this.$store.state.featureCollections.forEach((fc) => {
				this.displayFeatures(fc);
			});
		},
		addDrawControl(control_name) {
			if (!this.drawControl) {
				this.drawnItems = new L.FeatureGroup().addTo(map);
				this.layercontrol.addOverlay(this.drawnItems, control_name);
				this.drawControl = new L.Control.Draw({
					edit: {
						featureGroup: this.drawnItems,
                        edit:false
					},
					draw: {
						polygon: true, // {allowIntersection: false,showArea: true,},
						polyline: true,
						rectangle: false,
						circlemarker: false,
						circle: false,
						marker: true,
					},
				});
				this.drawControl.addTo(map);
			}

			map.on(L.Draw.Event.CREATED, (event) => {
				var layer = event.layer;

				layer.properties = {};
				var feature = (layer.feature = layer.feature || {});
				feature.type = "Feature";
				feature.properties = feature.properties || {};

				let marker_name = prompt("Name Plekje", " ");

				if (marker_name != null && marker_name != " ") {
					feature.properties["name"] = marker_name;

					layer.bindTooltip(feature.properties["name"]);

					this.drawnItems.addLayer(layer);
                    if(this.$store.state.edittingPlekollection){
                        this.$store.state.featureCollectionToEdit.features.push(layer.toGeoJSON());
                    }
				}
			});

            map.on('draw:deleted',(e)=>{
                console.log(e)
            })
		},
	},
	mounted() {
		this.setupLeafletMap();
        // EDIT PLEKOLLECTION MODE
		if (this.$store.state.edittingPlekollection) {
			this.addDrawControl(this.$store.state.featureCollectionToEdit.name);
			this.displayFeatures(this.$store.state.featureCollectionToEdit);
		}
        // CREATE PLEKOLLECTION MODE
		if (this.$store.state.creatingPlekollection) {
			this.addDrawControl("new plekollection");
		}
        // GLOBAL EMITTER EVENT LISTENERS
		this.emitter.all.clear();
		this.emitter.on("updateFeatureCollectionsShownOnMap", () => {
			this.displayFeatureCollections();
		});
		this.emitter.on("saveNewPlekollection", (name) => {
			let geojson_feature_collection = this.drawnItems.toGeoJSON();
			if (geojson_feature_collection.features.length != 0) {
				api.launchCreatePlekollectionFlow(
					geojson_feature_collection,
					name
				);
				this.$store.state.creatingPlekollection = false;
                this.$store.commit("reloadMap");
			} else {
				alert("Create plekjes to save first");
			}
		});
		this.emitter.on("saveEdittedPlekollection", () => {
			let geojson_feature_collection = this.drawnItems.toGeoJSON();
			if (geojson_feature_collection.features.length != 0) {
				api.launchEditPlekollectionFlow(geojson_feature_collection);
			} else {
				alert("Create plekjes to save first");
			}
		});
	},
	beforeUnmount() {
		if (map) {
			map.remove();
		}
	},
};
</script>

<style scoped>
#mapContainer {
	width: 100%;
	height: calc(100% - 46px);
	position: fixed;
	top: 46px;
}
</style>