<template>
	<div class="menu">
		<div class="card">
			<h1>Plekjes</h1>
			<p>V0.6 Pre-alpha</p>
		</div>
		<div v-if="this.$store.state.creatingPlekollection">
			<div class="card">
				<p>new Plekollection</p>
				<span>name: <input v-model="new_plekollection_name" /></span>
				<br /><br />
				<button class="btn" @click="saveNewPlekollection()">
					save
				</button>
				<br /><br />
			</div>
		</div>
		<div v-if="this.$store.state.userData">
			<div class="card">
				<h2>My plekollections</h2>
				<div
					v-if="
						this.$store.state.userData.my_plekollections.length > 0
					"
				>
					<div
						v-for="(p, i) in this.$store.state.userData
							.my_plekollections"
						class="list"
					>
						<div
							@click="selectPlekollection(i)"
							v-bind:class="{ selected: i == selected_i }"
						>
							{{ p.name }}
							<button
								v-if="
									this.$store.state.featureCollections
										.map((x) => x.props.name)
										.includes(p.name)
								"
								@click="removeFromMap(p, i)"
							>
								x
							</button>
						</div>
					</div>
				</div>
				<div v-else-if="!this.$store.state.creatingPlekollection">
					<button
						class="btn"
						@click="
							this.$store.state.creatingPlekollection = true;
							this.$store.commit('reloadMap');
						"
					>
						New plekollection
					</button>
				</div>
				<br />
			</div>
			<div class="card" v-if="this.$store.state.selectedPlekollection">
				<h2>{{ this.$store.state.selectedPlekollection.name }}</h2>
				<button class="btn" @click="showSelectedPlekollectionOnMap()">
					Show on map
				</button>
				&nbsp;
				<button
					v-if="
						this.$store.state.edittingPlekollection &&
						this.$store.state.featureCollectionToEdit &&
						this.$store.state.selectedPlekollection.name ==
							this.$store.state.featureCollectionToEdit.props.name
					"
					class="btn"
					@click="SaveEdittedPlekollection()"
				>
					Save
				</button>
				<button v-else class="btn" @click="editSelectedPlekollection()">
					Edit
				</button>
				<br /><br />
                <!-- DISPLAY PLEKJES WHEN EDITING -->
				<div
					v-if="
						this.$store.state.edittingPlekollection &&
						this.$store.state.featureCollectionToEdit &&
						this.$store.state.selectedPlekollection.name ==
							this.$store.state.featureCollectionToEdit.props.name
					"
					class="list"
					v-for="f in this.$store.state.featureCollectionToEdit.features"
				>
                <div >
						{{ f.properties.name }}
						<button
							v-if="this.$store.state.edittingPlekollection"
							@click="removeFromFeatureCollectionToEdit(f)"
						>
							-
						</button>
					</div>
                </div>
                <!-- DISPLAY PLEKJES WHEN NOT EDITING -->
				<div v-else 
                    class="list"
					v-for="p in this.$store.state.selectedPlekollection.plekjes">
                    {{ p.name }}
                </div>

				<br />
			</div>
		</div>
		<div v-else>Loading...</div>
	</div>
</template>

<script>
var api = require("@/service");
export default {
	data() {
		return {
			selected_i: null,
			new_plekollection_name: null,
			plekollection_to_edit: { name: null },
		};
	},
	methods: {
		selectPlekollection(i) {
			this.selected_i = i;
			let plekollection_id =
				this.$store.state.userData.my_plekollections[i]._id;
			api.getSelectedPlekollectionData(plekollection_id);
		},
		saveNewPlekollection() {
			if (
				!this.new_plekollection_name |
				(this.new_plekollection_name == " ")
			) {
				alert("please enter a plekollection name");
			} else {
				this.emitter.emit(
					"saveNewPlekollection",
					this.new_plekollection_name
				);
			}
		},
		showSelectedPlekollectionOnMap() {
			if (this.$store.state.edittingPlekollection) {
				this.$store.state.edittingPlekollection = false;
				this.$store.commit("reloadMap");
			}
			if (!this.$store.state.showMap) {
				this.$store.state.showMap = true;
			}

			api.setFeatureCollectionToShow(() => {
				this.emitter.emit("updateFeatureCollectionsShownOnMap");
			});
		},
		removeFromMap(p) {
			this.$store.commit("filterFeatureCollections", p.name);
			this.emitter.emit("updateFeatureCollectionsShownOnMap");
		},
		editSelectedPlekollection() {
			this.$store.state.creatingPlekollection = false;
			this.$store.state.edittingPlekollection = true;
			this.$store.state.featureCollections = [];

			api.setFeatureCollectionToEdit(() => {
				this.$store.commit("reloadMap");
			});
		},
		removeFromFeatureCollectionToEdit(feature) {
			if (
				confirm(
					`Delete "${feature.properties.name}" from "${this.$store.state.featureCollectionToEdit.props.name}"?`
				)
			) {
				console.log("deleting");
				this.$store.state.featureCollectionToEdit.features =
					this.$store.state.featureCollectionToEdit.features.filter(
						(f) => {return f.properties.name != feature.properties.name}
					);
				this.$store.commit("reloadMap");
			} else {
				console.log("canceled");
			}
		},
		SaveEdittedPlekollection() {
			this.emitter.emit("saveEdittedPlekollection");
		},
	},
	mounted() {
		if (!this.$store.state.userData) {
			this.$store.dispatch("setUserData");
		}
	},
};
</script>

<style>
.menu {
	width: 25%;
}
.card {
	width: 90%;
	text-align: left;
	margin: 5px auto;
	padding: 0px 5px;
	border: solid;
	border-width: 1.5px;
	border-radius: 5px;
	color: #333;
}

.list div:hover {
	background-color: #ddd;
	color: #333;
}
.list div.selected {
	background-color: #ddd;
	color: #333;
}

.list button {
	float: right;
	border: none;
	padding: 0 5px;
	font-size: 16px;
	font-weight: bold;
	color: #dc3545;
	background-color: inherit;
}
.btn {
	background-color: #17a2b8;
	color: #f2f2f2;
	padding: 10px 15px;
	font-size: 16px;
	border: none;
	border-radius: 5px;
}
.btn:hover {
	color: #333;
}
</style>