<template>
	<div class="home">
		<div v-if="this.$store.state.showMenu">
			<SideMenu v-if="this.$store.state.userEmail != ''" />
			<div
				v-else-if="!this.$store.state.userIsAuthenticated"
				v-bind:style="{ textAlign: 'left', margin: 10 + 'px' }"
			>
				<button class="btn" @click="this.$store.dispatch('auth0Login')">Log in</button>
			</div>
			<div v-else v-bind:style="{ textAlign: 'left', margin: 10 + 'px' }">
				Loading...
			</div>
		</div>
		<div v-if="this.$store.state.showMap" :key="this.$store.state.reloadMap">
			<LeafletMap></LeafletMap>
		</div>
		<div v-else>
			<!-- <h1>Hello</h1> -->
		</div>
	</div>
</template>

<script>
import LeafletMap from "@/components/LeafletMap.vue";
import SideMenu from "@/components/SideMenu.vue";
export default {
	name: "Home",
	components: { LeafletMap, SideMenu },
	data() {
		return {};
	},
	beforeCreate() {
		if (this.$store.state.userEmail == "") {
			this.$store.dispatch("auth0getUserEmail");
		}
		if (!this.$store.state.userIsAuthenticated) {
			this.$store.state.showMenu = false;
		}
	},
};
</script>
