import { createStore } from "vuex";
import auth0 from "auth0-js";
import router from "./router";
var api = require("@/service");

export default createStore({
	state: {
        //auth
        auth0: new auth0.WebAuth({
			domain: process.env.VUE_APP_AUTH0_CONFIG_DOMAIN,
			clientID: process.env.VUE_APP_AUTH0_CONFIG_CLIENTID,
			redirectUri: process.env.VUE_APP_DOMAINURL + "/auth0callback",
			responseType: process.env.VUE_APP_AUTH0_CONFIG_RESPONSETYPE,
			scope: process.env.VUE_APP_AUTH0_CONFIG_SCOPE,
			audience: process.env.VUE_APP_AUTH0_CONFIG_API_IDENTIFIER,
		}),
        userEmail:"",
		userData: null,
        userToBeCreated:false,
        userIsAuthenticated: false,
        // component state
        reloadMap:false,
		showMap: true,
		showMenu: true,
        creatingPlekollection:false,
        edittingPlekollection:false,
        // data
        featureCollectionToEdit:null,
        featureCollections:[],
        selectedPlekollection:null
	},
	mutations: {
		setUserIsAuthenticated(state, replacement) {
			state.userIsAuthenticated = replacement;
		},
        reloadMap(state){
            state.reloadMap = !state.reloadMap
        },
        filterFeatureCollections(state,name){
            state.featureCollections = state.featureCollections.filter(fc=>{
                return fc.props.name!=name
            })
        }
	},
	actions: {
        auth0Login(context) {
			context.state.auth0.authorize();
		},
		auth0HandleAuthentication(context) {
			context.state.auth0.parseHash((err, authResult) => {
				if (
					authResult &&
					authResult.accessToken &&
					authResult.idToken
				) {
					let expiresAt = JSON.stringify(
						new Date().getTime() + authResult.expiresIn * 1000
					);
					localStorage.setItem(
						"access_token",
						authResult.accessToken
					);
					localStorage.setItem("id_token", authResult.idToken);
					localStorage.setItem("expires_at", expiresAt);
					context.dispatch("auth0getUserEmail");
					router.replace("/profile");
					if (err) {
						alert("login failed");
						router.replace("/");
						console.log(err);
					}
				}
			});
		},
		auth0getUserEmail(context) {
            if(localStorage.getItem("access_token")){
                context.state.auth0.client.userInfo(
                    localStorage.getItem("access_token"),
                    function (err, user) {
                        if(user){
                            context.state.userEmail = user.email;
                        }     
                    }
                );
            }
		},
		auth0Logout(context) {
			localStorage.removeItem("access_token");
			localStorage.removeItem("id_token");
			localStorage.removeItem("expires_at");
			//this.userProfile=null;

			window.location.href =
				process.env.VUE_APP_AUTH0_CONFIG_DOMAINURL +
				"/v2/logout?returnTo=" +
				process.env.VUE_APP_DOMAINURL +
				"&client_id=" +
				process.env.VUE_APP_AUTH0_CONFIG_CLIENTID;
		},
        setUserData(context,callback) {
            const query = JSON.stringify({
				email: context.state.userEmail,
			});
			api.apiCall(
				"POST",
				process.env.VUE_APP_API_URL+"/users/get_one",
				query,
				(e) => {
					let mongodata = JSON.parse(e);
					if (mongodata){
                        context.state.userData = mongodata;
					}else if(router.currentRoute.value.name=='Profile'){
                        context.state.userToBeCreated=true;
                    }
				}
			);
		},
    },
	modules: {},
});
