import Store from "./store";

export function apiCall(type, path, body, callback) {
	//console.log("calling API");
	var xobj = new XMLHttpRequest();
	xobj.open(type, path, true);
	xobj.setRequestHeader("Content-Type", "application/json");
	xobj.setRequestHeader(
		"Authorization",
		`Bearer ${localStorage.getItem("access_token")}`
	);
	xobj.send(body);
	xobj.onreadystatechange = () => {
		if (xobj.readyState == 4 && xobj.status == "200") {
			//console.log("API called");
			callback(xobj.responseText);
		}
	};
}

// SHOW & EDIT HELPER FUNCTIONS
export function getSelectedPlekollectionData(plekollection_id) {
	apiCall(
		"GET",
		process.env.VUE_APP_API_URL +
			"/plekollections/get_one/" +
			plekollection_id,
		null,
		(e) => {
			if (e) {
				let mongodata = JSON.parse(e);
				Store.state.selectedPlekollection = mongodata;
			}
		}
	);
}

export function setFeatureCollectionToShow(callback) {
	// IF PLEKOLLECTION NOT ALREADY IN STATE
	if (
		!Store.state.featureCollections
			.map((x) => x.props.name)
			.includes(Store.state.selectedPlekollection.name)
	) {
		getPlekjesDataFromselectedPlekollection((fc) => {
			Store.state.featureCollections.push(fc);
			callback();
		});
	}
}

export function setFeatureCollectionToEdit(callback) {
	getPlekjesDataFromselectedPlekollection((fc) => {
		Store.state.featureCollectionToEdit = fc;
		callback();
	});
}

const getPlekjesDataFromselectedPlekollection = (callback) => {
	let list_of_ids = Store.state.selectedPlekollection.plekjes.map(
		(x) => x._id
	);
	getListOfPlekjesFromListOfIds(list_of_ids, (res) => {
		callback(createFeatureCollection(res));
	});
};

const createFeatureCollection = (plekjes) => {
	let geojsonFeatureCollection = {
		type: "FeatureCollection",
		props: {
			name: Store.state.selectedPlekollection.name,
			id: Store.state.selectedPlekollection._id,
			// in future more if you need them
		},
		features: [],
	};
	plekjes.forEach((e) => {
		geojsonFeatureCollection.features.push({
			type: "Feature",
			properties: {
				name: e.name,
				id: e._id,
				// in future more if you need them
			},
			geometry: e.geometry,
		});
	});
	return geojsonFeatureCollection;
};

const getListOfPlekjesFromListOfIds = (list_of_ids, callback) => {
	const query = JSON.stringify({ ids: list_of_ids });
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/get_multiple/",
		query,
		(e) => {
			let mongodata = JSON.parse(e);
			callback(mongodata);
		}
	);
};

// CREATE PLEKOLLECTION FLOW
export function launchCreatePlekollectionFlow(featcol, name) {
	saveFeatColAsPlekjes(featcol, name);
}
const saveFeatColAsPlekjes = (featcol, name) => {
	let new_plekjes = [];
	for (let i = 0; i < featcol.features.length; i++) {
		new_plekjes.push({
			name: featcol.features[i].properties.name,
			//description:"",
			tags: [name],
			founder: Store.state.userData.username,
			//createdAt
			//updatedAt
			geometry: featcol.features[i].geometry,
		});
	}
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/add_multiple",
		JSON.stringify(new_plekjes),
		(e) => {
			savePlekjesAsPlekollection(JSON.parse(e), name);
		}
	);
};
const savePlekjesAsPlekollection = (plekjes, name) => {
	let newPlekcollection = {
		name: name,
		//description:"",
		//tags: [],
		collector: Store.state.userData.username,
		//createdAt
		//updatedAt
		plekjes: plekjes,
	};
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekollections/new",
		JSON.stringify(newPlekcollection),
		(e) => {
			updateUser(JSON.parse(e), plekjes);
		}
	);
};
const updateUser = (plekollection, new_plekjes, plekjes_to_remove) => {
	const query = JSON.stringify({
		email: Store.state.userEmail,
	});
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/users/get_one",
		query,
		(e) => {
			let user = JSON.parse(e);

			// add new plekollection to my_plekollection
			if (plekollection) {
				user.my_plekollections.push({
					name: plekollection.name,
					_id: plekollection._id,
				});
			}
            // remove deleted plekjes
            let ids_to_remove = plekjes_to_remove.map(x=>x._id)
            user.my_plekjes = user.my_plekjes.filter(
                (x) => {
                    return !ids_to_remove.includes(x._id);
                }
            );
            // add new plekjes to my_plekjes
            if(new_plekjes){
                user.my_plekjes = user.my_plekjes.concat(new_plekjes);
            }
			
			// update user
			apiCall(
				"PUT",
				process.env.VUE_APP_API_URL + "/users/update/" + user._id,
				JSON.stringify(user),
				(e) => {
					Store.dispatch("setUserData");
				}
			);
		}
	);
};

// EDIT PLEKOLLECTION FLOW
export function launchEditPlekollectionFlow(featcol) {
	let new_features = [];
	Store.state.featureCollectionToEdit.features.forEach((feature) => {
        // no id == new plekje
		if (!feature.properties.id) {
			new_features.push(feature);
		}
	});

	if (new_features.length > 0) {
		saveNewPlekjes(
			new_features,
			(new_plekjes) => {
				updatePlekollection(new_plekjes);
			}
		);
	} else {
		updatePlekollection();
	}
}
const saveNewPlekjes = (features, callback) => {
	let new_plekjes = [];
	for (let i = 0; i < features.length; i++) {
		new_plekjes.push({
			name: features[i].properties.name,
			geometry: features[i].geometry,
			tags: [Store.state.featureCollectionToEdit.name],
			founder: Store.state.userData.username,
		});
	}
	apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/add_multiple",
		JSON.stringify(new_plekjes),
		(e) => {
			callback(JSON.parse(e));
		}
	);
};
const deletePlekjes = (ids) =>{
    apiCall(
        "DELETE",
        process.env.VUE_APP_API_URL +
            "/plekjes/delete_multiple",
        JSON.stringify({ ids: ids }),
        (e) => {}
    );
}
const updatePlekollection = (new_plekjes) => {
	apiCall(
		"GET",
		process.env.VUE_APP_API_URL +
			"/plekollections/get_one/" +
			Store.state.featureCollectionToEdit.props.id,
		null,
		(e) => {
			if (e) {
				let plekollection = JSON.parse(e);

				// check if plekollection needs to be updated
				let array1 = plekollection.plekjes.map((x) => x.name);
				let array2 = Store.state.featureCollectionToEdit.features.map((x) => x.properties.name);
				if (array1.sort().join(",") === array2.sort().join(",")) {
					//dont update
					Store.state.edittingPlekollection = false;
					Store.commit("reloadMap");
				} else {
					//do update

					// delete removed plekjes
					let ids_to_remain = Store.state.featureCollectionToEdit.features
						.map((x) => x.properties.id)
						.filter((x) => x);
                    let plekjes_to_keep = []
                    let plekjes_to_remove = []
                    plekollection.plekjes.forEach(p=>{
                        if(ids_to_remain.includes(p._id)){
                            plekjes_to_keep.push(p)
                        }else{
                            plekjes_to_remove.push(p)
                        }
                    })
                    plekollection.plekjes = plekjes_to_keep
					deletePlekjes(plekjes_to_remove.map(x=>x._id));

                    // add new plekjes
					if (new_plekjes) {
						plekollection.plekjes =
							plekollection.plekjes.concat(new_plekjes);
					}
                    plekollection.updatedAt = new Date().toISOString()

					//update plekollection
					apiCall(
						"PUT",
						process.env.VUE_APP_API_URL +
							"/plekollections/update/" +
							Store.state.featureCollectionToEdit.props.id,
						JSON.stringify(plekollection),
						(e) => {
                            getSelectedPlekollectionData(Store.state.featureCollectionToEdit.props.id)
							updateUser(null, new_plekjes,plekjes_to_remove);
							Store.state.edittingPlekollection = false;
							Store.commit("reloadMap");
						}
					);
				}
			}
		}
	);
};
