<template>
	<div class="nav">
		<router-link to="/">Home</router-link>
		<span v-if="this.$route.name == 'Home'" class="buttongroup">
			<!-- <a class="line">I</a> -->
			<a
				class="button"
				@click="toggleMap()"
				v-bind:class="{ active: this.$store.state.showMap }"
			>
				Map
			</a>
			<a
				class="button"
                @click="toggleMenu()"
				v-bind:class="{ active: this.$store.state.showMenu }"
			>
				Menu
			</a>
			<a
                v-if="this.$store.state.userIsAuthenticated"
				class="button"
				@click="initCreatePlekollection()"
				v-bind:class="{ active: this.$store.state.creatingPlekollection}"
			>
				New
			</a>
		</span>
		<router-link to="/about">About</router-link>
		<router-link v-if="this.$store.state.userIsAuthenticated" to="/profile">Profile</router-link>
		<span class="log">
			<a @click="login()" v-if="!this.$store.state.userIsAuthenticated">Login</a>
			<a @click="logout()" v-if="this.$store.state.userIsAuthenticated">Logout</a>
		</span>
	</div>
</template>

<script>
export default {
	data() {
		return {
		};
	},
	methods: {
		login() {
			this.$store.dispatch('auth0Login');
		},
		logout() {
			this.$store.dispatch('auth0Logout');
		},
        toggleMap(){
            if(!this.$store.state.creatingPlekollection){
                this.$store.state.showMap = !this.$store.state.showMap
            }
        },
        toggleMenu(){
            this.$store.state.edittingPlekollection = false;
            this.$store.state.showMenu = !this.$store.state.showMenu
            this.$store.commit("reloadMap")
        },
        initCreatePlekollection(){
            this.$store.state.edittingPlekollection = false;
            this.$store.state.creatingPlekollection = !this.$store.state.creatingPlekollection;
                if(this.$store.state.creatingPlekollection){
                    this.$store.state.showMenu=true;
                }
            this.$store.state.featureCollections=[];
            this.$store.commit("reloadMap")
        }
	},
};
</script>

